import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ContentProvider} from '../../providers/content/content';

/**
 * Generated class for the BatchEditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-batch-edit',
  templateUrl: 'batch-edit.html',
})
export class BatchEditPage {

  events: string;

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private contentProvider: ContentProvider) {
    contentProvider.getAllEvents().then((events) => {
      this.events = JSON.stringify(events);
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BatchEditPage');
  }

  updateEvents() {
    this.contentProvider.importData(JSON.parse(this.events));
  }
}



